  // 根据getStraightInviteAddress返回值调用，查询指定账户中的holdings和level数据
  async getUserStatistics ({commit}, userAddressArr) { // userAddressArr 账户地址
    const myProvider = Vue.prototype.$myProvider
    const networkVersion = myProvider.networkVersion
    if (networkVersion === Vue.prototype.$ethConf.networkVersion) {
      const contractRes = Vue.prototype.$myContracts && Vue.prototype.$myContracts.contractRes
      if (contractRes) {
        let addressLen = userAddressArr.length
        let begin = 0
        for (let j = 0; j < 100; j++) {
          if (addressLen > 10) {
            const teamData = []
            for (let i = 0; i < 10; i++, begin++) { // 每次最多获取十条数据
              addressLen--
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error('getUserStatistics交易发生错误')
                } else {
                  // console.log('getUserStatistics交易的返回值', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 投资数量
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 等级
                  teamData.push({
                    address: userAddress, // todo 这里可能有异步回调问题，回调时begin可能并不是一开始的那个值;如果出现，可以尝试定义const变量控制,经过尝试const可以和回调绑定
                    holdings: holdings,
                    level: level
                  })
                  if (i === 9) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
            }
          } else {
            const teamData = []
            for (let i = 0; i < addressLen; i++, begin++) {
              const userAddress = userAddressArr[begin]
              contractRes.methods.getPersonalStatistics().call({
                from: userAddress
              }, function (error, result) {
                if (error) {
                  console.error('getUserStatistics交易发生错误')
                } else {
                  // console.log('getUserStatistics交易的返回值', result)
                  let holdings = ethUtils.weiToEth(result.holdings) // 投资数量
                  holdings = ethUtils.numFloatLen(holdings, 4)
                  const level = result.level // 等级
                  teamData.push({
                    address: userAddress, // todo 这里可能有异步回调问题，回调时begin可能并不是一开始的那个值
                    holdings: holdings,
                    level: level
                  })
                  if (i === (addressLen - 1)) {
                    commit('SET_TEAMDATA', teamData)
                  }
                }
              })
              if (i === (addressLen - 1)) {
                j = 1000
              }
            }
          }
        }
      }
    }
  },
